/* App.css */

/* Reset default browser styles */
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
font,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
  min-height: 100%;
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

body {
  line-height: 1;
  min-height: 100vh;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

/* Global styles */
body {
  font-family: 'Raleway', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-size: 16px;
  line-height: 1.6;
  font-weight: 300;
}

/* Header styles */
.background-video {
  width: 100%;
  height: auto;
  object-fit: cover;
}

.page-header {
  position: relative;
  overflow: hidden;
} 

.intro {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  z-index: 2;
  color: #fff;
}

.intro .project-name {
  font-size: 36px;
  font-weight: 400;
  margin-bottom: 10px;
}

.intro .highlight {
  font-size: 48px;
  font-weight: 700;
  margin-bottom: 20px;
  color: #89CFEF;
}

.intro .project-tagline {
  font-size: 24px;
  font-weight: 300;
  margin-bottom: 20px;
}

.intro .btn {
  display: inline-block;
  padding: 12px 24px;
  font-size: 18px;
  font-weight: 500;
  text-decoration: none;
  background-color: #1C2951;
  color: #fff;
  border-radius: 4px;
  transition: background-color 0.3s ease;
}

.intro .btn:hover {
  background-color: #F4BCC3;
}

/* Navigation styles */
nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 1000;
  transition: top 0.3s ease;
}

nav.sticky {
  top: 0;
}

nav ul {
  display: flex;
  justify-content: center;
  list-style-type: none;
  flex-direction: row;
}

nav ul li {
  margin: 15px 15px;
}

nav ul li a {
  text-decoration: none;
  color: #fff;
  font-size: 18px;
  font-weight: 300;
  transition: color 0.3s ease;
  list-style-type: none;
}

nav ul li a:hover {
  color: #F4BCC3;
}

/* Main content styles */
main {
  padding: 100px 0;
  background-color: #fff;
  color: #333;
}

.container {
  margin: 0 auto;
  width: 1000px;
  padding: 20px 30px;
}

h2 {
  font-size: 36px;
  font-weight: 700;
  margin-bottom: 20px;
  justify-content: center;
}
h3 {
  font-size: 24px;
  font-weight: 700;
  justify-content: center;
}

p {
  font-size: 18px;
  font-weight: 300;
  margin-bottom: 20px;
}

ul li {
  font-size: 18px;
  font-weight: 300;
  margin-bottom: 10px;
}

ul li::before {
  margin-right: 5px;
}

/* Youtube Video styles */
.video-responsive {
  overflow: hidden;
  padding-bottom: 56.25%;
  position: relative;
  width: 100%;
}

.video-responsive iframe {
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  position: absolute;
}

a {
  color: #007bff;
  text-decoration: none;
  font-weight: 400;
  transition: color 0.3s ease;
}

a:hover {
  color: #0056b3;
}

/* Barchart styles */
.barchart {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 400px;
  height: 300px;
}

/* Footer styles */
.site-footer {
  background-color: #1C2951;
  text-align: center;
  color: white;
  width: 100%;
}

.site-footer h2 {
  font-size: 36px;
  font-weight: 700;
  margin-bottom: 20px;
  color: white;
}

.site-footer p {
  font-size: 18px;
  font-weight: 300;
  margin-bottom: 20px;
}

.site-footer a:hover {
  color: white;
}

.download-button {
  margin-top: 10px;
  font-size: 20px;
  font-weight: 700;
  padding: 10px 20px;
  background-color: #EC8390; /* Change the background color as desired */
  color: #fff; /* Text color */
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.download-button:hover {
  background-color: #F1A7B0; /* Change the hover background color as desired */
}

/* Adjust the icon size and spacing */
.download-button i {
  margin-right: 5px; /* Spacing between icon and text */
  font-size: 18px; /* Icon size */
}

.site-footer-social {
  flex-direction: row;
  gap: 20px;
  margin-top: 10px;
}

.site-footer-social:hover{
  color: white;
}

.site-footer-owner {
  font-size: 14px;
  color: #aaa;
}

.site-footer-owner a {
  color: #aaa;
}

/* Media Queries */
@media screen and (max-width: 600px) {
  .container {
    margin: 10px;
    max-width: none;
    padding: 0;
  }
}

/* scrollbar styling */

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  height: 0px;
  width: 8px;
  border: 1px solid #fff;
}

::-webkit-scrollbar-track {
  border-radius: 0;
  background: #eeeeee;
}

::-webkit-scrollbar-thumb {
  border-radius: 0;
  background: #b0b0b0;
}

/* Add animation classes for navigation menu items */
.nav-item {
  transition: color 0.3s ease, transform 0.3s ease;
}
.nav-item.active {
  color: #007bff; /* Change the color to highlight the active item */
  transform: scale(1.1); /* Apply a scale effect */
}

/* Add animation classes for containers */
.container {
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 0.5s ease, transform 0.5s ease;
}
.container.active {
  opacity: 1;
  transform: translateY(0);
}